<template>
    <div class=wrap>
        <div class="section">
            <h1>내일 할 일</h1>
            <div class="inputSection">
                <div class="inputSectionButton">
                    <input v-model="span">
                    <button>+</button>
                </div>
            </div>
            <div class=listSection>
                <ul>
                    <li>{{ span }}
                        <button @click="del">D</button>
                        <button>{{ edit }}</button>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'Todolist',
  data () {
    return {
      edit: 'E',
      span: ''
    }
  },
  methods: {
  }
}
</script>

<style scoped>
h1 {
    margin: 10px;
    color: #e3e3e3;
}
.wrap {
    display: flex;
    width: 100%;
    height: 100%;
}
.section {
    margin: auto;
    width: 500px;
    height: 800px;
    margin-top: 80px;
    border: 2px solid gray;
    border-radius: 20px;
    background-color: #165710;
}
.section ul {
    border: 1px solid gray;
    width: 100%;
    height: 100%;
    border-radius: 20px;
    background-color: #ffffff;
}
.section ul li {
    margin: 15px;
    padding-bottom: 10px;
    border-bottom: 1px solid #165710;
}
.section ul li button {
    width: 30px;
    float: right;
    height: 18px;
}
.inputSection {
    display: flex;
}
.inputSectionButton {
    margin: auto;
}
.inputSectionButton input {
    padding-left: 10px;
}
.section input {
    width: 250px;
    height: 30px;
}
.section button {
    width: 40px;
    height: 30px;
}
.listSection {
    padding: 15px;
    box-sizing: border-box;
    height: calc(100% - 97px);
}

</style>



